<template>
  <div>
    <div class="gameStart" v-if="!statue.start">
      <form @submit.prevent="statGame">
        <input type="text" v-model="user.name" placeholder="請輸入暱稱">
        <input type="submit" value="確定" :disabled="user.name === ''">
      </form>
    </div>
  </div>
</template>

<script>
import firebase from 'firebase/app';

export default {
  name: 'Game',
  data() {
    return {
      user: {
        id: '',
        name: '',
      },
      statue: {
        start: false,
      },
    };
  },
  methods: {
    statGame() {
      const vm = this;
      const startTime = new Date();
      const timestamp = Math.floor(startTime / 1000);
      vm.user.id = timestamp + vm.user.name;
      firebase.database().ref(`notLogin/${vm.user.id}`).set({
        name: vm.user.name,
        startTime: String(startTime),
        endTime: '未完成',
      });
      this.$bus.$emit('user:name', vm.user);
      vm.$router.push('play');
    },
  },
};
</script>

